.table {
    margin-top: 20px;
    height: 490px;
    overflow: scroll;
    overflow-x: hidden;
    color: #6a5d5d;
    background-color: white;
}
.table tr {
    display: flex;
    justify-content: space-between;
}
.table td {
    padding: 0.5rem;
}
.table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
}